import React, { Suspense } from 'react';
import { Switch } from 'react-router-dom';
import { routes, SubRoutes } from '@/router';

// AuthRoute  route . 1
function App() {
  //   route . 1
  //  route  Container 

  return (
    <Suspense fallback={<span>&nbsp;</span>}>
      <Switch>
        {routes.map(route => {
          return <SubRoutes key={route.path} {...route} />;
        })}
      </Switch>
    </Suspense>
  );
}

export default App;
