// AuthRoute    component .
//  route @/App.js .
// refs = https://reacttraining.com/react-router/web/guides/static-routes

//  route routes  .
import { lazy } from 'react';
import { NotFound, Login } from '@/pages';

const routes = [
  {
    path: '/',
    name: 'root',
    exact: true,
    redirect: '/dashoboard',
    meta: {
      navigation: {
        show: false,
      },
    },
  },
  {
    path: '/dashboard/:time?',
    name: 'dashboard',
    component: lazy(() => import('@/components/dashboard/Container')),
    meta: {
      navigation: {
        show: true,
        breadTitle: '  '
      },
    },
  },
  {
    path: '/detail',
    name: 'detail',
    component: lazy(() => import('@/components/detail/Container')),
    meta: {
      navigation: {
        show: true,
        breadTitle: '  '
      },
    },
  },
  {
    path: '/login',
    name: "login (Don't use Layout)",
    component: Login,
    meta: {
      navigation: {
        show: true,
      },
    },
  },
  {
    path: '*',
    component: NotFound,
    meta: {
      navigation: {
        show: false,
      },
    },
  },
];

export default routes;
